.tree-page {
  display: grid;
  grid-template-areas: 'a b c';
  grid-template-columns: minmax(200px, 1fr) 620px minmax(200px, 1fr);
  grid-template-rows: 1fr;
  width: 100%;
  height: 100%;
}
.settings-section {
  grid-area: 'a';
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  padding: 20px 10px 0;
}
.tree-section {
  grid-area: 'b';
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  background: white;
  background-blend-mode: multiply;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  transition: background-image 600ms ease-in-out;
}
.item-section {
  grid-area: 'c';
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  padding: 20px 10px 0;
}

.controls-container {
  display: flex;
  column-gap: 30px;
}
.volume-button,
.snow-button {
  display: block;
  width: 40px;
  height: 40px;
  filter: brightness(0.5);
  background-repeat: no-repeat;
  background-size: contain;
  transition: filter ease 150ms;
}
.volume-button.active,
.snow-button.active {
  filter: none;
}

.choose-tree-container,
.bg-container,
.garland-container,
.active-toys-container,
.made-tree-wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  width: 100%;
}
.choose-tree-title,
.bg-title,
.garland-title,
.active-toys-title,
.made-tree-title {
  font-size: 24px;
  text-transform: uppercase;
}
.choose-tree-list,
.bg-list,
.active-toys-list {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}
.garland-list {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}
.choose-tree-item {
  cursor: pointer;
  display: block;
  width: 80px;
  height: 80px;
  border: 1px solid skyblue;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.5);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: border ease 150ms;
}
.choose-tree-item.active {
  border: 3px solid skyblue;
}
.bg-item {
  cursor: pointer;
  display: block;
  width: 60px;
  height: 60px;
  border: 1px solid skyblue;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.5);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: border ease 150ms;
}
.bg-item.active {
  border: 3px solid skyblue;
}
.garland-item {
  cursor: pointer;
  display: block;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: linear-gradient(124deg, #ff2400, #e81d1d, #e8b71d, #e3e81d, #1de840, #1ddde8, #2b1de8);
  transition: box-shadow ease 150ms;
}
.garland-item:hover {
  cursor: pointer;
  -webkit-box-shadow: 0px 0px 24px 7px rgb(124, 124, 124);
  -moz-box-shadow: 0px 0px 24px 7px rgb(108, 109, 109);
  box-shadow: 0px 0px 24px 7px rgb(97, 99, 99);
}
.garland-item.active {
  -webkit-box-shadow: 0px 0px 24px 7px rgb(51 114 109);
  -moz-box-shadow: 0px 0px 24px 7px rgba(51, 114, 109, 1);
  box-shadow: 0px 0px 24px 7px rgb(51 114 109);
}

.garland-toggler {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 30px;
  border: 1px solid white;
  border-radius: 15px;
  background-color: rgb(214, 100, 100);
  transition: background-color ease 250ms;
}
.garland-toggler.active {
  background-color: rgb(97, 207, 69);
}
.active-toys-item {
  cursor: pointer;
  position: relative;
  width: 60px;
  height: 60px;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.5);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}
.active-toys-item.inactive {
  cursor: default;
  background-image: none !important;
  pointer-events: none;
}
.active-toys-item.inactive > .active-toys-count {
  display: none;
}
.active-toys-count {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  bottom: 3px;
  right: 3px;
  width: 18px;
  height: 18px;
  color: black;
  border-radius: 50%;
  background-color: skyblue;
}
.made-tree-container {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}
.made-tree-slider {
  display: flex;
  overflow: hidden;
  width: 100%;
}
.made-tree-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.made-tree-item {
  cursor: pointer;
  display: block;
  width: 60px;
  height: 60px;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.5);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}
.toy-on-tree {
  cursor: pointer;
  position: absolute;
  z-index: 100;
  display: block;
  width: 60px;
  height: 60px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  pointer-events: none;
}
.toy-on-tree.draggable {
  pointer-events: auto;
}
.tree-img {
  display: block;
  width: 500px;
  height: 714px;
}

.reset-settings-button,
.state-button {
  padding: 5px 10px;
  border-radius: 15px;
  border: 1px solid skyblue;
  transition: background ease 150ms;
}
.reset-settings-button:hover,
.state-button:hover {
  background: #0bc80b94;
}
